<!--  DOM  -->
<template>
    <div class="popup-box isvip" v-if="mask">
        <div class="box-con">
            <div class="top-box" v-if="isLogin">
                <span>{{txt}}</span>
            </div>
            <div class="top-box" v-else>
                <span></span>
            </div>
            <div class="bom-box">
                <span class="canel" @click="canelFn"></span>
                <template v-if="isLogin">
                    <span class="sure" @click="sureFn"></span>
                </template>
                <template v-else>
                    <span class="sure" @click="jumpLogin"></span>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'isvip', // 
    data() {
        return {
            // 
            isLogin:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        val:{
            default:''
        },
        mask:{
            default:false
        },
        txt:{
            default:''
        }

    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        // let str=sessionStorage.getItem('permissions');
        // this.dataArr=str;
        if(sessionStorage.getItem('xtoken')){
            this.isLogin=true;
        }
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        // if(this.dataArr.indexOf(this.val)!=-1){
            // this.isTrue=true;
        // }
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        sureFn(){//APP
            this.modalHelper.beforeClose();
            this.$router.push('/openingVip');
        },
        canelFn(){
            this.modalHelper.beforeClose();
            this.$parent.isvip=false;
        },
        jumpLogin(){
            this.modalHelper.beforeClose();
            this.$router.push('/logo');
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.isvip{
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,.5);
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    .box-con{
        width: 548px;
        height: 291px;
        border-radius: 10px;
        background: #fff;
    }
    .top-box{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 196px;
        span{
        width: 360px;
        display: inline-block;
        color: #999;
        }
    }
    .bom-box{
        border-top: 1PX solid #F2F2F2;
        height: 90px;
        line-height: 90px;
        display: flex;
        span{
        display: inline-block;
        width: calc((100% - 1PX)/2);
        text-align: center;
        }
        .canel{
        border-right: 1PX solid #F2F2F2;
        color: #333;
        }
        .sure{
        color: #FE6603
        }
    }
}
</style>



