<!--  DOM  -->
<template>
    <div class="membership">
        <top-back :title='""'></top-back>
        <ul class="box">
            <li>
                <h5>-</h5>
                <div>
                    <p class="tit"></p>
                    <p></p>
                </div>
                <div>
                    <p class="tit"></p>
                    <p></p>
                </div>
                <div>
                    <p class="tit"></p>
                    <p></p>
                </div>
            </li>
            <li v-for="(o,i) of list" :key="i">
                <h5>{{o.title}}<span v-if="o.name">{{o.name}}</span></h5>
                <p>{{o.content}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
import topBack from '@/components/topback'
export default {
    name: 'membership', // 
    data() {
        return {
            // 
            list:[
                {
                    title:'',
                    name:'APP',
                    content:''
                },{
                    title:'',
                    name:'APP',
                    content:''
                },{
                    title:'',
                    name:'APP',
                    content:''
                },{
                    title:'',
                    name:'APP',
                    content:''
                },{
                    title:'',
                    name:'APP',
                    content:''
                },{
                    title:'',
                    content:''
                },{
                    title:'',
                    name:'APP',
                    content:''
                },
            ]
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.membership{
    background: #f5f5f5;
    padding-top: 90px;
    // min-height: calc(100vh - 191px);
    padding-bottom: 101px;
}
.box{
    li{
        background: #fff;
        margin-bottom: 16px;
        padding: 30px;
        h5{
            font-size: 32px;
            padding-left: 20px;
            border-left: 6px solid #FE6603;
            box-sizing: border-box;
            margin-bottom: 30px;
            span{
                font-size: 24px;
                font-weight: normal;
            }
        }
        div:last-child{
            margin-bottom: 0;
        }
        div{
            margin-bottom: 29px;
            .tit{
                font-size: 28px;
                color: #333;
                margin-bottom: 23px;
            }
        }
        p{
            font-size: 28px;
            color: #999;
        }
    }
    
}
</style>



