<!--  DOM  -->
<template>
    <div class="logo">
       <top-back :title='name'></top-back>
       <img src="../../assets/relogo.png" alt="">
       <div class="input-box">
         <input v-model="username" placeholder="" @blur.prevent="inputLoseFocus"/>
       </div>
        <div class="prompt"  >
          <span v-show="enname">
             
          </span>
        </div>
        <div class="input-box">
          <input v-model="password" type='password' placeholder="" @blur.prevent="inputLoseFocus"/>
        </div>
         <div class="pr-forget"  >
          <div>
           <span v-show="enword">
            (8)
           </span>
          </div>
          <div class="color" @click="jump" >
            
          </div>
        </div>
        <div class="logo-btn" @click="register" >
          
        </div>
        <div class="logo-btn reg" @click="jumpto" >
          
        </div>
        <div class="toast"  v-if="isShow" >
           
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
let sha1 = require("sha1");
// import toast from '@/components/toast/toast.js';  
export default {
    name: 'logo', // 
    data() {
        return {
            // 
          name:'',
          username:'',
          password:'',
          enname:false,
          enword:false,
          isShow:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    // mounted() {
    //     // console.group('===============mounted');
    //     this.$nextTick(function() {
    //         // console.log('===============mounted');
    //     });
    // },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    // beforeDestroy() {
    //     // console.group('  ===============beforeDestroy');
    // },
    destroyed() {
        // console.group('===============destroyed');
    },

    methods: {
        //  
        register() {
            if(!(/^1[3|4|5|7|8][0-9]\d{8,11}$/.test(this.username.trim()))) {
               return this.enname = true
            }
            if(this.password.trim() == '') {
                return this.enword = true
            }
        let that=this;
        this.$http({
            method:'post',
            url: '/authorize/memberLogin',
            data:{
              phoneNo:that.username,
              loginPwd:sha1(that.password),
              channel:'1004',
              clientVersion:'3.0'
            }
        }).then(function(res){
            if(res.data.code == '1' ) {
                sessionStorage.setItem('xtoken',res.data.data.xtoken)
                sessionStorage.setItem('Bname',res.data.data.nikeName)
                sessionStorage.setItem('permissions',res.data.data.permissions);
                sessionStorage.setItem('userid',res.data.data.pkid);
                if(sessionStorage.getItem('path')&&sessionStorage.getItem('path')!=null){
                  let uri=sessionStorage.getItem('path');
                  that.$router.replace(uri)
                }else{
                  that.$router.replace('home')
                }
            } else {
                that.isShow = true;
                setTimeout(() => {
                   that.isShow = false;
                 }, 2000);
            }  
        })

        },
        inputLoseFocus() {
          // setTimeout(() => {
          //   window.scrollTo(0, 0);
          // }, 200);
        },
        jumpto() {
            this.$router.push('/enroll')
        },
        jump() {
             this.$router.push('/find')
        }
    },
    mounted() {
    },
    beforeDestroy() {
    },


}

</script>
<!--  "scoped"   CSS  -->
<style lang='less' scoped>
.logo {
  min-height: 100vh;
  background: #fff;
  padding: 90px 92px;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
//   overflow:scroll;
  img {
    margin: 115px 0 119px;
    width: 138px;
    height: 116px;
  }

  .pr-forget {
     width: 100%;
    font-size: 28px;
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
    margin-bottom: 150px;
    span {
      color: red;
    }
  }
  
 
}
</style>



