<!--  DOM  -->
<template>
    <div class="zhongbCon newNotice">
        <div @click="topush(obj)" >
            <div class="top">
                <p class="icon">
                    <img src="../assets/icon-zhong.png"/>
                </p>
                <p class="tit">{{obj.title}}</p>
            </div>
            <div class="center">{{obj.oneName ? obj.oneName : '' }}</div>
            <div class="bottom">
                <p>{{obj.oneOffer ? obj.oneOffer + '' : '' }}</p>
                <p>{{obj.opendate||obj.openDate ? obj.opendate||obj.openDate : '' }}</p>
            </div>
        </div>
        <template v-if="follow">
            <div class="follow-box">
                <span @click="noFollow"></span>
            </div>
        </template>
        <v-dia v-if="isload"></v-dia>

    </div>
</template>
<script>

import dialog from '@/components/dialog'
export default {
    name: 'zhongbCon', // 
    data() {
        return {
            // 
            isload:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        follow:{
            default:false
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        // this.conver()
        if( sessionStorage.getItem('permissions') == null || sessionStorage.getItem('permissions') == '' || sessionStorage.getItem('permissions').indexOf('bidFilter') == -1  ) {
            if(this.obj.oneName){
                this.obj.oneName=this.getPassOnename(this.obj.oneName)
            }
            if( this.obj.oneOffer) {
                this.obj.oneOffer=this.getPassOneoffer(this.obj.oneOffer)
            } 
        }
    },
    components: {
        'v-dia':dialog
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        conver() {
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate = this.obj.opendate +  ''             
        },
        topush(o) {
            if(sessionStorage.getItem('xtoken')) {
                this.$router.push({path:'/notice',query:{id:o.id,source:o.source}})
            } else {
                this.isload = true 
            }
            
        },
        noFollow(){
            this.$emit('nofollow',{id:this.obj.id,source:this.obj.source})
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
</style>



