<!--  DOM  -->
<template>
    <div class="openingVip">
        <top-back :title='""'></top-back>
        <!--  -->
        <div class="user-box card">
            <div class="user-img">
                <img :src="userinfo.imageUrl " alt="" v-if="userinfo.imageUrl">
                <img src="../../assets/icon-tpux.png.png" alt="" v-else>
            </div>
            <div class="user-center">
                <div class="cen-name">
                    <span>{{userinfo.nikeName}}</span>
                    <span class="label">{{userinfo.state}}</span>
                </div>
                <div class="cen-day">
                    <span class="color">{{userinfo.day}}</span>
                </div>
            </div>
        </div>
        <h5 class="title"></h5>
        <!--  -->
        <div class="main card">
            <ul>
                <li v-for="(o,i) of viplist" :key="i" :class="{'active':i==tabNum}" @click="tabFn(i)">
                    <p class="time">{{o.stdDesc}}</p>
                    <p class="money">{{o.price}}</p>
                    <p class="save">{{o.save}}</p>
                    <div class="discount" v-if="o.altInfo">{{o.altInfo}}</div>
                </li>
            </ul>
            <div class="bottom-box">
                <button @click="jumpPay" class="openBtn">{{openTxt}}</button>
                <button @click="$router.push('/membership')" class="privilegeBtn"></button>
                <!-- <p @click="$router.push('/membership')">  ></p> -->
            </div>
        </div>
        <!--  -->
        <div class="card item">
            <h5></h5>
            <p>10731-85076077</p>
            <p>2</p>
            <p>3</p>
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
export default {
    name: 'openingVip', // 
    data() {
        return {
            // 
            userinfo:{//

            },
            viplist:[],
            tabNum:0,
            openTxt:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        if(sessionStorage.getItem('payOrder')){
            this.tabNum=JSON.parse(sessionStorage.getItem('payOrder')).num;
        }
        //
        let that=this;
        this.$http({
            method:'post',
            url: '/vip/queryFeeStandard',
            data:{
                channel:'1004',
            }
        }).then(function(res){
            for(let x of res.data.data){
                x.save=x.primePrice-x.price
            }
            that.viplist=res.data.data
        }).catch(function(res){
            
        })
        this.getUser();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        getUser(){//
            let that=this;
            this.$http({
                method:'post',
                url: '/userCenter/refreshUserInfo',
                data:{}
            }).then(function(res){
                that.userinfo = res.data.data
                let gap = new Date( that.userinfo.expiredDate).getTime() - new Date().getTime()
                that.userinfo.day = Math.ceil(gap/3600/24/1000) >= 0 ? Math.ceil(gap/3600/24/1000) : 0; 
                if( that.userinfo.roleName == '') {
                    that.userinfo.state = '';
                    that.openTxt='';
                } else {
                    that.userinfo.state = '';
                    that.openTxt=''
                }

            })
        },
        tabFn(i){
            this.tabNum=i
        },
        jumpPay(){
            let data={
                num:this.tabNum
            };
            sessionStorage.setItem('payOrder',JSON.stringify(data));
            this.$router.push('/payVip');
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.openingVip{
    background: #f5f5f5;
    padding-top: 110px;
    padding-bottom: 121px;
    // min-height: calc(100vh - 282px);
}
.card{
    box-sizing: border-box;
    background: #fff;
    width: 90%;
    margin: 0 auto;
    padding:0 35px;
    box-shadow:0px 3px 9px 1px rgba(4,0,0,0.1);
    border-radius:8px;
}
/**/ 
.user-box{
    display: flex;
    height: 178px;
    // margin-bottom: 20px;
    align-items: center;
    .user-img {
        width: 139px;
        height: 139px;
        margin-right: 40px;
        border-radius: 50%;
        overflow: hidden;
        img {
            width: 100%;
        }
    }
    .cen-name{
        margin-bottom: 22px;
        span{
            font-weight: bold
        }
        .label {
          font-size: 24px;
          background-color: #E5FFE1;
          padding: 9px;
          color: #66D554;
          margin-left: 10px;
        }
    }
    .cen-day{
        font-size: 24px;
        color:#999;
    }
}
.title{
    height: 88px;
    line-height: 88px;
    font-size: 36px;
    color: #4D3A3A;
    width: 90%;
    margin: 0 auto;
}
/**/
.main{
    margin-bottom: 25px;
    padding-bottom: 20px;
    ul{
        display: flex;
        margin-bottom: 50px;
        padding-top: 36px;
        justify-content: space-between;
        flex-wrap: wrap;
        .active{
            border: 2PX solid #FF0000
        }
        li{
            width: calc((100% - 30px)/2);
            border: 2PX solid #F0F0F0;
            margin-bottom: 30px;
            height: 222px;
            overflow: hidden;
            position: relative;
            // padding: 20px;
            padding-top: 50px;
            border-radius: 10px;
            box-sizing: border-box;
            p{
                text-align: center;
                margin-bottom: 10px;
            }
            .discount{
                text-align: center;
                width: 200px;
                height: 50px;
                line-height: 50px;
                font-size: 36px;
                background: orange;
                color: #fff;
                position: absolute;
                top: 0;
                right: 0;
                transform: rotateZ(45deg) translate(25%,-50%);

            }
            .save{
                font-size: 24px;
                color: #999;
                margin-bottom: 0;
            }
            .money{
                font-size: 50px;
                color: #FE6603;
            }
            .time{
                color: #4D3A3A;
                font-size: 24px;
            }
        }
    }
    .bottom-box{
        text-align: center;
        button{
            width: 100%;
            line-height: 92px;
            height: 92px;
            border-radius: 92px;
            border: none;
            margin-bottom: 30px;
        }
        .openBtn{
            color: #fff;
            background: #FE6603;
        }
        .privilegeBtn{
            background: #fff;
            color: #FE6603;
            border: 1PX solid #FE6603;
        }
        p{
            font-size: 24px;
            color:deepskyblue;
            text-align: right;
        }
    }
}
/**/
.item{
    padding:30px 36px 38px;
    h5{
        text-align: center;
        color: #FE6603;
        font-size: 32px;
        margin-bottom: 28px;
    }
    p{
        font-size: 28px;
        color: #786D6D
    }
}
</style>



