<!--  DOM  -->
<template>
    <div class="zbContent newNotice" >
        <div @click="topush(obj)">
            <div class="top">
                <p class="icon">
                    <img src="../assets/icon-zhao.png"/>
                </p>
                <p class="tit">{{obj.title}}</p>
            </div>
            <div class="center">{{obj.certificate ? obj.certificate : ''}}</div>
            <div class="bottom">
                <p>{{obj.pbMode ? obj.pbMode : ''}}</p>
                <p>{{obj.opendate||obj.openDate ? obj.opendate||obj.openDate : ''}}</p>
            </div>
        </div>
        
        <template v-if="follow">
            <div class="follow-box">
                <span @click="noFollow"></span>
            </div>
        </template>
         <v-dia v-if="isload"></v-dia>
    </div>
</template>
<script>
// let moment = require("moment");
import dialog from '@/components/dialog'
export default {
    name: 'zbContent', // 
    data() {
        return {
            // 
             isload:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        follow:{
            default:false
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // this.conver()
        // console.group('===============created');
        if( sessionStorage.getItem('permissions') == null || sessionStorage.getItem('permissions') == '' || sessionStorage.getItem('permissions').indexOf('bidFilter') == -1  ) {
            if(this.obj.certificate){
                this.obj.certificate=this.getPassCertificate(this.obj.certificate)
            }
            if( this.obj.pbMode) {
                this.obj.pbMode=this.getPassPbMode(this.obj.pbMode)
            } 
        }
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    components: {
        'v-dia':dialog
    },
    mounted() {
        // console.group('===============mounted');
        
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        // 
        conver() {
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate = this.obj.opendate +  ''             
        },
        topush(o) {
             if(sessionStorage.getItem('xtoken')) {
                 this.$router.push({path:'/article',query:{id:o.id,source:o.source}})
            } else {
                this.isload = true 
            }
        },
        noFollow(){
            this.$emit('nofollow',{id:this.obj.id,source:this.obj.source})
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">

</style>



