<!--  DOM  -->
<template>
  <div class="top-nav">
        <van-icon name="arrow-left" class="top-left" @click="$router.go(-1)" />
        {{title}}
        <p v-if="isOrder" @click="mask=true"></p>
        <v-popup :mask="mask"></v-popup>
   </div>
</template>
<script>
// let moment = require("moment");
import popup from '@/components/popup'
export default {
    name: 'topBack', // 
    data() {
        return {
            // 
            mask:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        title:'',
        isOrder:{
            default:false
        }
    },
    components: {
       'v-popup':popup,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
   
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.top-nav {
    height: 90px;
    width: 100%;
    background-color: #FE6603;
    line-height: 90px;
    text-align: center;
    color:#fff;
    font-size: 36px;
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    .top-left {
        font-size: 50px;
        color:#fff;
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
    }
    p{
        color: #fff;
        position: absolute;
        right:35px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 24px
    }
}
</style>



