<!--  DOM  -->
<template>
    <div class="followBtn">
        <div class="nofollow" @click="nofollowFn" v-if="!isFollow">+</div>
        <div class="follow" @click="followFn" v-else></div>
    </div>
</template>
<script>
export default {
    name: 'followBtn', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    computed:{
        isFollow(){
            if(this.collected){
                return true;
            }else{
                return false;
            }
        }
    },
    props: {
        // 
        type:{
            default:''
        },
        id:{
            default:''
        },
        collected:{
        },
        source:{
            default:''
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        
        this.$nextTick(function() {
            // console.log('===============mounted');
            
            
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        followFn(){//
            let that=this;
            if(this.type!='qy'){
                this.$http({
                    method:'post',
                    url:'/userCenter/cancelCollectionNotice',
                    data:{
                        source:that.source,
                        noticeid:that.id,
                    }
                }).then(function(res){
                    that.$parent.collected=false;
                })
            }else{
                this.$http({
                    method:'post',
                    url:'/userCenter/cancelCollectionCompany',
                    data:{
                        companyid:that.id,
                    }
                }).then(function(res){
                    that.$parent.collected=false;
                })
            }
        },
        nofollowFn(){//
            if(this.type=='zhaob'){//   type=0
                let that=this;
                this.$http({
                    method:'post',
                    url:'/userCenter/collectionNotice',
                    data:{
                        source:that.source,
                        type: "0",
                        noticeid:that.id,
                    }
                }).then(function(res){
                    that.$parent.collected=true;
                })
            }else if(this.type=='zhongb'){//  type=2
                let that=this;
                this.$http({
                    method:'post',
                    url:'/userCenter/collectionNotice',
                    data:{
                        source:that.source,
                        type: "2",
                        noticeid:that.id,
                    }
                }).then(function(res){
                    that.$parent.collected=true;
                })
            }else{//
                let that=this;
                this.$http({
                    method:'post',
                    url:'/userCenter/collectionCompany',
                    data:{
                        companyid:that.id,
                    }
                }).then(function(res){
                    if(res.data.code==1){
                        that.$parent.collected=true;
                    }
                })
            }
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.nofollow{
    width: 100px;
    height: 40px;
    border: 1PX solid #FE6603;
    background: #fff;
    color: #FE6603;
    font-size: 28px;
    text-align: center;
    border-radius: 10px;
    line-height: 40px;
}
.follow{
    width: 100px;
    height: 40px;
    border: 1PX solid #FE6603;
    background: #FE6603;
    color: #fff;
    font-size: 28px;
    text-align: center;
    border-radius: 10px;
    line-height: 40px;
}
</style>



