<!--  DOM  -->
<template>
    <div class="enroll">
         <top-back :title='name'></top-back>
         <img src="../../assets/relogo.png" alt="">
         <div class="input-box">
           <input v-model="username" placeholder="" @blur.prevent="inputLoseFocus" />
         </div>
        
         <div class="prompt en-mb"  >
           <span v-show="enname">
              
           </span>
         </div>
         <div class="input-box">
           <input v-model="note" center clearable placeholder="" @blur.prevent="inputLoseFocus"/>
           <span slot="button" type="primary" @click="gainCode" >{{Message}}</span>
         </div>
        
           
         <div class="prompt en-mb"  >
           <span v-show="enword">
             {{msg}}
           </span>
         </div>
         <div class="input-box">
           <input v-model="password" type='password' placeholder="(8)" @blur.prevent="inputLoseFocus" />
         </div>
         
       <div class="en-hint">
         <span class="color" @click="deal" ></span>
       </div>
         <div class="logo-btn en-top" @click="register"  >
          
        </div>
         <div class="toast"  v-if="isShow" >
           {{hint}}
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
let sha1 = require("sha1");
export default {
    name: 'enroll', // 
    data() {
        return {
            // 
            name:'',
            username:'',
            enname:false,
            enword:false,
            Message:'',
            password:'',
            isShow:false,
            hint:'',
            note:'',
            msg:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
         inputLoseFocus() {
          // setTimeout(() => {
          //   window.scrollTo(0, 0);
          // }, 200);
        },
        deal() {
          this.$router.push('/deal')
        },
        gainCode() {
            if(!(/^1[3|4|5|7|8][0-9]\d{8,11}$/.test(this.username.trim()))) {
               return this.enname = true
            }
            if(!(this.Message  == '') && !(this.Message  == '')  ) {
              return
            } else {
             
              let that=this;
              this.$http({
                  method:'post',
                  url: '/authorize/getVerifyCode',
                  data:{
                    type:1,
                    invitationPhone:that.username
                  }
              }).then(function(res){
                  that.Message = 60
                  if(res.data.code == 1 ) {

                  } else {
                    that.isShow = true
                    that.hint = res.data.msg
                     setTimeout(() => {
                       that.isShow = false;
                     }, 2000);
                  }
                   var myVar = setInterval(() =>{
                      if(that.Message == 0) {
                        that.Message = ''
                        return clearInterval(myVar);
                      } else {
                        that.Message =  that.Message - 1
                      }
                    },1000 )
              })
            }
        },
        register() {
           if(!(/^1[3|4|5|7|8][0-9]\d{8,11}$/.test(this.username.trim()))) {
              return this.enname = true
           }
            if(this.note.trim() == '') {
              this.msg = ''
              return this.enword = true
           }
            if(this.password.trim() == '' ||  !(/[0-9A-Za-z]{8,16}$/.test(this.password)) ) {
              this.msg = '816'
              return this.enword = true
           }
             let that=this;
              this.$http({
                  method:'post',
                  url: '/authorize/memberRegister',
                  data:{
                    verifyCode:that.note,
                    phoneNo:that.username,
                    loginPwd:sha1(that.password),
                    channel:'1004',
                    clientVersion:'3.0'
                  }
              }).then(function(res){
                 if(res.data.code ==1 ) {
                    that.isShow = true
                    that.hint = res.data.msg
                     setTimeout(() => {
                       that.isShow = false;
                     }, 2000);
                     that.$router.push('/logo')
                 } else {
                   that.isShow = true
                    that.hint = res.data.msg
                     setTimeout(() => {
                       that.isShow = false;
                     }, 2000);
                 }
                 
              })
        }     
            
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.enroll {
  min-height: 100vh;
  background: #fff;
  padding: 90px 92px;
  display: flex;
  align-items: center;
  flex-direction: column;
   img {
    margin: 115px 0 119px;
    width: 138px;
    height: 116px;
  }
  .en-mb {
    margin-bottom: 10px;
    height: 30px;
  }
  .en-top {
    margin-top: 155px;
  }
  .en-hint {
    width: 100%;
    font-size: 24px;
    color:#999;
    margin: 10px 0;
  }

}

</style>



