<!--  DOM  -->
<template>
    <div class="zhongbCon newNotice">
        <div @click="topush(obj)">
            <div class="top">
                <p class="icon">
                    <img src="../assets/icon-qi.png"/>
                </p>
                <p class="tit">{{obj.comName}}</p>
            </div>
            <div class="center">{{obj.legalPerson}}</div>
            <div class="center">{{obj.phone}}</div>
            <div class="center">{{obj.comAddress}}</div>
        </div>
        <template v-if="follow">
            <div class="follow-box">
                <span @click="noFollow"></span>
            </div>
        </template>
        <v-dia v-if="isload"></v-dia>
    </div>
</template>
<script>
import dialog from '@/components/dialog'
export default {
    name: 'zhongbCon', // 
    data() {
        return {
            // 
              isload:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        follow:{
            default:false
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    components: {
        'v-dia':dialog
    },
    methods: {
        //  .
         topush(o) {
             if(sessionStorage.getItem('xtoken')) {
                this.$router.push({path:'/letter',query:{id:o.comId,source:o.regisAddress,name:o.comName}})
            } else {
                this.isload = true 
            }
            
        },
        noFollow(){
            this.$emit('nofollow',{id:this.obj.comId})
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
</style>



