<!--  DOM  -->
<template>
    <div class="popup-box popup" v-if="mask">
        <div class="box-con">
            <div class="top-box">
                <p></p>
                <p>
                    <a style="color:#FE6603" href="tel:0731-85076077">0731-85076077</a>
                </p>
            </div>
            <div class="bom-box">
                <span class="sure" @click="canelFn"></span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'popup', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        mask:{
            default:false
        },
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        // if(this.dataArr.indexOf(this.val)!=-1){
            // this.isTrue=true;
        // }
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        canelFn(){
            this.modalHelper.beforeClose();
            this.$parent.mask=false;
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.popup{
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,.5);
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    .box-con{
        width: 548px;
        height: 291px;
        border-radius: 10px;
        background: #fff;
    }
    .top-box{
        height:196px;
        font-size: 32px;
        padding-top:50px; 
        box-sizing: border-box;
        p{
            height: 70px;
            line-height: 70px;
        }
    }
    .bom-box{
        border-top: 1PX solid #F2F2F2;
        height: 90px;
        line-height: 90px;
        display: flex;
        span{
        display: inline-block;
        width: 100%;
        text-align: center;
        }
        .canel{
        border-right: 1PX solid #F2F2F2;
        color: #333;
        }
        .sure{
            color: #FE6603
        }
    }
}
</style>



