<!--  DOM  -->
<template>
    <div class="dia"  >
       <div class="d-hint">
         <h3></h3>
         <p class="d-hi">
           ,
         </p>
         <div class="btn">
            <!-- <div class="d-btn left" @click="cancel" >
              
            </div> -->
            <div class="d-btn left"  @click="jump" >
              
            </div>
         </div>
       </div>
      <v-load  ></v-load>
    </div>
</template>
<script>
import loading from '@/components/loading'
export default {
    name: 'dia', // 
    data() {
        return {
            // 
          
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'v-load':loading
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        cancel() {
          this.isall = false
        },
        jump() {
          this.$router.push('/logo')
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.dia {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  .d-hint {
    width: 80%;
    position: fixed;
    top: 35%;
    left: 50%;
    transform: translateX(-50%);
    height: 300px;
    background-color: #fff;    
    z-index: 9999999;
    display: flex;
    flex-direction:column;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 8px;
    .d-hi {
      margin-top: 50px;
      color: #999;
    }
    .btn {
      position: absolute;
      left: 0;
      bottom: 0;
      height: 80px;
      width: 100%;
      border-top: 1px solid #f5f5f5;
      .d-btn {
        line-height: 80px;
        text-align: center;
        font-size: 32px;
        width: 100%;
        color: #FE6603;
        border-left: 1px solid #f5f5f5;
      }
    }
  }
}
</style>



